<template>
  <v-container>
    <div class="div-product_path">
      <span>
        <router-link :to="'/'">Home</router-link>
      </span>
      <span>/</span>
      <span>{{product.name}}</span>
    </div>

    <div class="d-flex div-product_img_info_wrapper">
      <!-- Product Images -->
      <div>
        <div class="div-product_img_list_wrapper" v-for="(productImage, i) in productImages" :key="i">
          <v-img :class="{active__detail: productImage.isActive}" :src="require(`@/assets/productImage/${productImage.path}`)" @click="switchingImage(i)" />
        </div>
      </div>
      <div class="div-product_img_wrapper">
        <v-img :src="require(`@/assets/productImage/${productImages[curShowImageIndex].path}`)"/>
      </div>
      <!-- Product Images End -->

      <div class="div-product_info">
        <!-- Product Info -->
        <div class="div-detail_product_name">{{product.name}}</div>
        <div class="div-detail_product_price">₩{{product.price.toLocaleString("ko-KR")}}</div>
        <div>{{product.description}}</div>
        <div :class="{read_more: readMoreActive}">
          Details:
          <ul>
            <li v-for="(productDetail, i) in productDetails" :key="i">{{productDetail}}</li>
          </ul>
        </div>
        <div class="div-read_more_btn" @click="readMore">{{readMoreText}}</div>
        <!-- Product Info End -->

        <!-- Select Size -->
        <div>
          <div>Size</div>
          <v-select
            class="select-select_size"
            label="Select size"
            :items="productSizes"
            item-text="size"
            item-disabled="disable"
            outlined
            dense
            :menu-props="{ bottom: true, offsetY: true }"
          ></v-select>
        </div>
        <!-- Select Size End -->

        <!-- Input Quantity -->
        <div class="mt-n6">
          <div>Quantity</div>
          <v-text-field
            class="v-text-field-input_quantity"
            outlined
            dense
            suffix="EA"
            v-model="defaultValue"
          >
          </v-text-field>
        </div>
        <!-- Input Quantity End -->

        <div class="d-flex mt-n4 div-add_to_btn_wrapper">
          <v-btn class="btn-add_to_cart" elevation="0" min-width="227" min-height="43">Add to Cart</v-btn>
          <v-btn class="btn-add_to_wish" elevation="0" max-width="43" min-width="43" min-height="43" outlined>
            <font-awesome-icon icon="fa-regular fa-heart" size="xl" style="color: #808080;" />
          </v-btn>
        </div>
      </div>
    </div>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      productId: null,
      product: null,
      productSizes: [],
      productImages: [],
      productDetails: [],
      curShowImageIndex: 0,
      defaultValue: 1,
      readMoreActive: true,
      readMoreText: "Read More"
    };
  },

  mounted() {
    this.productId = this.$route.query.productId;

    this.getProduct();
    this.getProductSizes();
    this.getProductImages();
    this.getProductDetails();
  },

  methods: {
    getProduct() {
      this.$axios.get(`/test5/${this.productId}`).then((res) => {
        this.product = res.data;
      });
    },

    getProductSizes() {
      this.$axios.get(`/test3/${this.productId}`).then((res) => {
        res.data.forEach(element => {
          this.productSizes.push({
            size: element.size,
            stock: element.stock,
            disable: element.stock < this.defaultValue ? true : false
          });
        });
      });
    },

    getProductImages() {
      this.$axios.get(`/test4/${this.productId}`).then((res) => {
        res.data.forEach(element => {
          this.productImages.push({
            path: element,
            isActive: false
          });
        });
      });
    },

    getProductDetails() {
      this.$axios.get(`/test6/${this.productId}`).then((res) => {
        res.data.forEach(element => {
          this.productDetails.push(element);
        });
      });
    },

    switchingImage(i) {
      this.productImages[this.curShowImageIndex].isActive = false;

      this.curShowImageIndex = i;
      this.productImages[this.curShowImageIndex].isActive = true;
    },

    readMore() {
      this.readMoreActive = !this.readMoreActive;
      this.readMoreText = this.readMoreActive ? "Read More" : "Read Less";
    }
  },
};
</script>

<style>
.div-product_path {
  margin: 50px 0 0 10px;
}

.div-product_path > span {
  padding-right: 5px;
}

.div-product_img_info_wrapper {
  justify-content: space-between;
  margin-top: 50px;
}

.div-product_img_list_wrapper {
  border: 1px solid black;
  margin: 10px;
  overflow: hidden;
}

.div-product_img_list_wrapper > .v-image {
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.active__detail {
  border: 1.5px solid black;
}

.div-product_img_wrapper {
  overflow: hidden;
  margin-top: 10px;
}

.div-product_img_wrapper .v-image {
  width: 500px;
  height: 560px;
}

.div-product_info {
  width: 280px;
}

.div-product_info > div {
  margin-bottom: 20px;
}

.div-detail_product_name {
  font-size: 30px;
  line-height: 36px;
  width: 280px;
}

.div-detail_product_price {
  font-size: 20px;
  line-height: 25px;
  color: #777777;
}

.div-read_more_btn {
  margin-top: -15px;
  font-size: 14px;
  text-decoration: underline;
  cursor: pointer;
}

.read_more {
  display: none;
}

.select-select_size {
  border-radius: 0px;
  width: 280px;
}

.v-text-field-input_quantity {
  border-radius: 0px;
  width: 80px;
}

.div-add_to_btn_wrapper > button {
  margin-right: 10px;
}

.btn-add_to_cart {
  background-color: black !important;
  color: white !important;
  border-radius: 0px !important;
  font-size: 17px;
  text-align: center;
}

.btn-add_to_wish {
  border-radius: 0px;
  text-align: center;
  border-color: #808080;
}
</style>